<template>
  <section class="mt-4 maxify">
    <div
      class="border-b-4 border-blue-400 justify-between flex"
    >
      <div
        class="flex tracking-wide font-semibold"
      >
        <div
          v-for="(x, i) in tab"
          :key="x"
          @click="
            active = i;
            selected = 0;
          "
          :class="
            `${
              i == active
                ? 'bg-blue-400 text-white hover:text-black'
                : 'hover:text-blue-500'
            } elmenu`
          "
        >
          {{ x }}
        </div>
      </div>
      <div class="flex select-none">
        <div
          @click="scrol(-getx)"
          class="elmenu hover:bg-blue-400 hover:text-white"
        >
          right
        </div>
        <div
          @click="scrol(getx)"
          class="elmenu hover:bg-blue-400 hover:text-white"
        >
          left
        </div>
      </div>
    </div>
    <div
      ref="scc"
      class="w-full scc mb-8 overflow-x-scroll"
    >
      <div
        class="flex mt-4 respo"
        :style="
          '--leng:' +
            table.length * 100 +
            '%'
        "
      >
        <div
          ref="ta"
          v-for="x in table"
          :key="x"
          class="pl-2 w-1/2 md:w-1/4"
        >
          <ImageS2 :el="table[x]" />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import ImageS2 from '@/C/images/ImageS2.vue';
export default {
  name: 'SectionS2',
  data() {
    return {
      active: 0,
      tab: ['Wahed', 'zodj', 'tlatha'],
      table: [0, 1, 2, 3, 4, 5, 6, 7]
    };
  },
  components: {
    ImageS2
  },
  methods: {
    scrol(x) {
      this.$refs.scc.scrollBy({
        top: 0,
        left:
          this.$refs.scc.scrollLeft == 0
            ? x + 8
            : x,
        behavior: 'smooth'
      });
    }
  },
  computed: {
    getx() {
      return this.$refs.ta.offsetWidth;
    }
  }
};
</script>
<style>
.scc::-webkit-scrollbar {
  display: none;
}
.respo {
  width: calc(var(--leng) / 2);
}
@media (min-width: 768px) {
  .respo {
    width: calc(var(--leng) / 4);
  }
}
.elmenu {
  @apply cursor-pointer px-3 md:px-6 py-1 rounded-t;
}
</style>
